<template>
  <div style="padding-top: 1.143rem;">
    <div class="pd-0-10 dis-flex">
      <van-image
        :src="yak_detail_content.yaks_img"
        fit="cover"
        style="width: 9.286rem;height: 7.857rem;"
      />
      <div class="flex-1 font-9" style="padding: 0.786rem 0 0.429rem;margin-left: 2.071rem;">
        <div class="font-12">
