        </div>
        <div class="color-656565 mg-top-10">耳标号：{{yak_detail_content.yaks_tag}}</div>
        <div class="mg-top-10">
          <span class="badge">{{yak_detail_content.age}}岁</span>
        </div>
        <div class="color-F7CA3B mg-top-10">
          <img
            src="../../assets/images/yak-adopt/position-yellow.png"
            class="icon-img"
            style="width: 0.857rem;height: 0.929rem;margin-right: 0.429rem;"
          />
        </div>
      </div>
    </div>

    <div class="pd-0-10 font-14 color-656565">
      <!--牦牛认养费用-->
      <div style="margin: 2.786rem auto 2.357rem;" class="divider-title">牦牛认养费用</div>
      <div class="tx-center">认养费用：12000/年/头</div>

      <!--认养回馈-->
      <div style="margin: 2.786rem auto 2.357rem;" class="divider-title">认养回馈</div>
      <p>1.每年可预计生育一头小牦牛，收益比颇高</p>
      <p>2.全年到牧场旅游食宿免费</p>
      <p>3.平台从认养费用中抽出一部分会以认养者名义进行爱心帮扶</p>
    </div>

