  </div>
</template>

<script>
import { yak_detail } from "../../api/api";
export default {
  name: "yak-adopt-confirm",
  data() {
    return {
      yak_detail_content:{},
    };
  },
  methods: {
    /**
     * @description: 提交-立即认养
     * @date 2020/2/12
     */
    onSubmit() {
      this.$router.push({
        path: "/common/pay-order"
      });
    }
  },
  created() {
    console.log(this.$route.query);
    let data = {
      yaks_id: this.$route.query.id
    };
    yak_detail(data).then(res => {
      this.yak_detail_content = res.data.data;
      console.log(res);
    });
  }
};
</script>

<style scoped lang="less">
/*标签*/
.badge {
  color: #989898;
  border-color: #979797;
  padding: 0 0.714rem;
